import React, { useEffect, useState, useRef } from "react";
import CourseDetailsComponent from "./CourseDetailsComponent";
import { courseOperations } from "app/store/state/active-course";
import { useDispatch, useSelector } from "react-redux";

function CourseDetails(props) {
  const [editMode, setEditMode] = useState(false);
  const [uploadMode, setUploadMode] = useState(false);
  const [deleteMode, setDeleteMode] = useState(false);
  const [redirectToLessons, setRedirectToLessons] = useState(false);
  const [redirectToModules, setRedirectToModules] = useState(false);
  const [isModified, setModifiedFlag] = useState(false);
  const [localCourseData, setLocalCourseData] = useState({});
  const activeCourse = useSelector((state) => state.activeCourse);
  const tinyMceApiKey = useSelector((state) => state.app.tinyMceApiKey);
  const dispatch = useDispatch();
  const activeCourseData = activeCourse.data;
  const prevActiveCourseDeletedSuccessRef = useRef();

  useEffect(() => {
    prevActiveCourseDeletedSuccessRef.current = activeCourse.deletedSuccess;
  });

  useEffect(() => {
    if (activeCourseData) {
      setModifiedFlag(false);
      setLocalCourseData(activeCourseData);
    }
  }, [activeCourseData]);

  useEffect(() => {
    const courseId = props.match.params.id;
    dispatch(courseOperations.loadCourse(courseId));
    // eslint-disable-next-line
  }, [props.match.params.id]);

  const toggleEditMode = () => {
    setEditMode(!editMode);
  };

  const toggleDeleteMode = () => {
    setDeleteMode(!deleteMode);
  };

  const openLessons = () => {
    setRedirectToLessons(true);
  };

  const openModules = () => {
    setRedirectToModules(true);
  };

  const handleChange = (field, e) => {
    e.stopPropagation();
    if (field) {
      const newValue = e.target.value;
      const oldValue = localCourseData[field];
      if (newValue !== oldValue) {
        setLocalCourseData({
          ...localCourseData,
          [field]: newValue,
        });
        if (!isModified) setModifiedFlag(true);
      }
    }
  };

  const handleSwitchChange = (field, e) => {
    console.log(field, e.target.checked);

    e.stopPropagation();
    if (field) {
      const newValue = e.target.checked;
      const oldValue = localCourseData[field];
      if (newValue !== oldValue) {
        setLocalCourseData({
          ...localCourseData,
          [field]: newValue,
        });
        if (!isModified) setModifiedFlag(true);
      }
    }
  };
  const publishCourse = (e, id, isPublished) => {
    e.stopPropagation();
    dispatch(courseOperations.publishCourseDetails(id, isPublished));
  };

  const deleteCourse = (e) => {
    e.stopPropagation();
    toggleDeleteMode();
    dispatch(courseOperations.deleteCourseDetails());
  };

  const saveCourseDetails = (e) => {
    e.preventDefault();
    console.log("saveCourseDetails > ", localCourseData);

    dispatch(courseOperations.saveCourseDetails(localCourseData));
  };

  const toggleUploadMode = (e) => {
    e.stopPropagation();
    setUploadMode(!uploadMode);
  };

  const onImageUpload = (imgUrl) => {
    setLocalCourseData({
      ...localCourseData,
      featuredImage: imgUrl,
    });
    if (!isModified) setModifiedFlag(true);
    setUploadMode(false);
  };

  const handleEditorChange = (field, overview) => {
    console.log("Overview was updated:", overview);
    setLocalCourseData({
      ...localCourseData,
      [field]: overview,
    });
    if (!isModified) setModifiedFlag(true);
  };

  return (
    <CourseDetailsComponent
      activeCourse={activeCourse}
      localCourseData={localCourseData}
      editModeEnabled={editMode}
      isModified={isModified}
      deleteModeEnabled={deleteMode}
      prevActiveCourseDeletedSuccessRef={prevActiveCourseDeletedSuccessRef}
      redirectToLessons={redirectToLessons}
      redirectToModules={redirectToModules}
      uploadMode={uploadMode}
      tinyMceApiKey={tinyMceApiKey}
      handleEditorChange={handleEditorChange}
      handleChange={handleChange}
      toggleDeleteMode={toggleDeleteMode}
      toggleEditMode={toggleEditMode}
      publishCourse={publishCourse}
      saveCourseDetails={saveCourseDetails}
      deleteCourse={deleteCourse}
      openLessons={openLessons}
      openModules={openModules}
      toggleUploadMode={toggleUploadMode}
      onImageUpload={onImageUpload}
      handleSwitchChange={handleSwitchChange}
    />
  );
}

export default CourseDetails;
