import React from "react";
import CourseCard from "app/views/common/components/CourseCard";
import Card from "react-bootstrap/Card";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBook,
  faBookOpen,
  faSitemap,
  faGlobe,
  faMoneyCheckAlt,
  faChalkboardTeacher,
  faImage,
  faSave,
  faUpload,
  faLayerGroup,
  faLaptopCode,
} from "@fortawesome/free-solid-svg-icons";
import DeleteConfirmation from "app/views/common/components/DeleteConfirmation";
import { Redirect, useRouteMatch } from "react-router-dom";
import NoDataFound from "app/views/common/components/NoDataFound";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import UploadImageModal from "app/views/common/components/UploadImageModal";
import { Editor } from "@tinymce/tinymce-react";
import { uploadImageFromBlobInfo } from "app/utils";

import "./course-details.scss";
import { LEVELS_MAP } from "app/constants/levels";

function CourseDetailsComponent(props) {
  const {
    activeCourse: { deletedSuccess },
    editModeEnabled,
    toggleEditMode,
    uploadMode,
    localCourseData,
    handleChange,
    isModified,
    publishCourse,
    saveCourseDetails,
    deleteModeEnabled,
    toggleDeleteMode,
    deleteCourse,
    prevActiveCourseDeletedSuccessRef,
    openLessons,
    openModules,
    redirectToLessons,
    redirectToModules,
    toggleUploadMode,
    onImageUpload,
    tinyMceApiKey,
    handleEditorChange,
    handleSwitchChange,
  } = props;
  let { url } = useRouteMatch();

  if (!localCourseData || localCourseData.length === 0)
    return <NoDataFound message="This course doesn't exist!" />;
  else if (!prevActiveCourseDeletedSuccessRef.current && deletedSuccess)
    return <Redirect to={`/courses`} />;
  else if (redirectToLessons) return <Redirect to={`${url}/lessons`} />;
  else if (redirectToModules) return <Redirect to={`${url}/modules`} />;
  else {
    const {
      id,
      name,
      overview,
      slug,
      url,
      price,
      author,
      featuredImage,
      isPublished,
      level,
      type,
      freeFlag,
    } = localCourseData;

    return (
      <div id="cfg_course_details_page" className="page p-0">
        <DeleteConfirmation
          visible={deleteModeEnabled}
          onCancel={toggleDeleteMode}
          onOk={deleteCourse}
          message={`Are you sure you want to delete this course?`}
        />
        <UploadImageModal
          visible={uploadMode}
          onCancel={toggleDeleteMode}
          onImageUpload={onImageUpload}
        />
        <Form onSubmit={saveCourseDetails}>
          <div className="course-header">
            <CourseCard
              mode="header"
              {...localCourseData}
              openLessons={openLessons}
              openModules={openModules}
              toggleEditMode={toggleEditMode}
              toggleDeleteMode={toggleDeleteMode}
            />
          </div>
          <div className="course-info p-3">
            <Card className="border-0">
              <Card.Body>
                <div className="text-muted">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faBook} className="mr-2" />
                      Title
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="name" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={name || ""}
                          placeholder="Enter title"
                          onChange={(e) => handleChange("name", e)}
                          required
                        />
                      </Form.Group>
                    ) : (
                      <>{name || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faBookOpen} className="mr-2" />
                      Overview
                    </strong>

                    {editModeEnabled ? (
                      <Form.Group controlId="overview" className="mb-0 mt-2">
                        <Editor
                          apiKey={tinyMceApiKey}
                          key={"form-wvgiwys"}
                          initialValue={overview || ""}
                          init={{
                            selector: "textarea",
                            images_upload_handler: function (
                              blobInfo,
                              success,
                              failure
                            ) {
                              uploadImageFromBlobInfo(
                                blobInfo,
                                success,
                                failure
                              );
                            },
                            height: 500,
                            menubar: false,
                            plugins: [
                              "advlist codesample autolink lists link image imagetools charmap print preview anchor",
                              "searchreplace visualblocks code fullscreen",
                              "insertdatetime media table paste code help wordcount",
                            ],
                            toolbar: `undo redo | formatselect | image | codesample | bold italic backcolor | 
             alignleft aligncenter alignright alignjustify | 
             bullist numlist outdent indent | removeformat | help`,
                            entity_encoding: "raw",
                          }}
                          onEditorChange={(overview) =>
                            handleEditorChange("overview", overview)
                          }
                        />
                      </Form.Group>
                    ) : (
                      <>{overview || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faSitemap} className="mr-2" />
                      Slug
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="slug" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={slug || ""}
                          placeholder="Enter slug"
                          onChange={(e) => handleChange("slug", e)}
                          required
                        />
                      </Form.Group>
                    ) : (
                      <>{slug || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faGlobe} className="mr-2" />
                      URL
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="url" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={url || ""}
                          placeholder="Enter url"
                          onChange={(e) => handleChange("url", e)}
                          required
                        />
                      </Form.Group>
                    ) : (
                      <>{url || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon
                        icon={faMoneyCheckAlt}
                        className="mr-2"
                      />
                      Pricing
                    </strong>
                    {editModeEnabled ? (
                      <>
                        <Form.Group controlId="price" className="mb-0 mt-2">
                          <Form.Label>Free?</Form.Label>
                          <Form.Check
                            type="switch"
                            label={freeFlag ? "Yes" : "No"}
                            id="custom-switch"
                            className="align-top ml-2"
                            checked={freeFlag}
                            onChange={(e) => handleSwitchChange("freeFlag", e)}
                            inline
                          />
                        </Form.Group>

                        {!freeFlag && (
                          <Form.Group controlId="price" className="mb-0 mt-2">
                            <Form.Control
                              type="number"
                              value={price || ""}
                              placeholder="Enter price"
                              onChange={(e) => handleChange("price", e)}
                              required
                            />
                          </Form.Group>
                        )}
                      </>
                    ) : (
                      <>
                        {typeof price === "number" || price
                          ? `INR ${price}`
                          : "N.A."}
                      </>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon
                        icon={faChalkboardTeacher}
                        className="mr-2"
                      />
                      Author
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="author" className="mb-0 mt-2">
                        <Form.Control
                          type="text"
                          value={author || ""}
                          placeholder="Enter author's name"
                          onChange={(e) => handleChange("author", e)}
                        />
                      </Form.Group>
                    ) : (
                      <>{author || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faLayerGroup} className="mr-2" />
                      Level
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="level" className="mb-0 mt-2">
                        <Form.Control
                          as="select"
                          value={level}
                          onChange={(e) => handleChange("level", e)}
                          custom
                        >
                          {Object.keys(LEVELS_MAP).map((key) => (
                            <option key={key} value={key}>
                              {LEVELS_MAP[key]}
                            </option>
                          ))}
                        </Form.Control>
                      </Form.Group>
                    ) : (
                      <>{LEVELS_MAP[level] || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="border-bottom mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faLaptopCode} className="mr-2" />
                      Type
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group controlId="type" className="mb-0 mt-2">
                        <Form.Control
                          as="select"
                          value={type}
                          onChange={(e) => handleChange("type", e)}
                          custom
                        >
                          <option value={"Rich text"}>Rich text</option>
                          <option value={"Video"}>Video</option>
                          <option value={"Rich text and video"}>
                            Rich text and video
                          </option>
                        </Form.Control>
                      </Form.Group>
                    ) : (
                      <>{type || "Not specified"}</>
                    )}
                  </div>
                </div>
                <div className="text-muted pt-3">
                  <div className="mb-0 pb-3">
                    <strong className="d-block text-gray-dark">
                      <FontAwesomeIcon icon={faImage} className="mr-2" />
                      Featured Image
                    </strong>
                    {editModeEnabled ? (
                      <Form.Group
                        controlId="featuredImage"
                        className="mb-0 mt-2"
                      >
                        <Row>
                          <Col>
                            <Button className="mb-2" onClick={toggleUploadMode}>
                              Upload
                            </Button>
                          </Col>
                        </Row>
                        <Row>
                          <Col>
                            <Form.Control
                              type="text"
                              readOnly={true}
                              value={featuredImage || ""}
                              placeholder="Enter image URL"
                              onChange={(e) => handleChange("featuredImage", e)}
                              required
                            />
                          </Col>
                        </Row>
                      </Form.Group>
                    ) : (
                      <div>{featuredImage || "Not specified"}</div>
                    )}

                    {!editModeEnabled && featuredImage && (
                      <Card.Img
                        variant="top"
                        src={featuredImage}
                        className="w-25 pt-2"
                      />
                    )}
                  </div>
                </div>
              </Card.Body>
            </Card>
          </div>
          <div className="course-action-panel">
            <div className="p-3">
              <Button
                type="submit"
                variant="success"
                className="ml-2"
                // onClick={saveCourseDetails}
                disabled={!isModified}
              >
                <FontAwesomeIcon icon={faSave} className="mr-2" />
                {isPublished ? "Update" : "Save"}
              </Button>

              <Button
                variant={"primary"}
                className="ml-2"
                onClick={(e) => publishCourse(e, id, !isPublished)}
                disabled={isModified}
              >
                <FontAwesomeIcon icon={faUpload} className="mr-2" />
                {isPublished ? "Un-publish" : "Publish"}
              </Button>
            </div>
          </div>
        </Form>
      </div>
    );
  }
}

export default CourseDetailsComponent;
